::-webkit-scrollbar {
  width: 2px; /*高宽分别对应横竖滚动条的尺寸*/
  height: 1px;
}

.header {
  height: 1.2rem;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 0 0.5rem;
  .add {
    cursor: pointer;
  }
}

.list {
  height: calc(100vh - 1.2rem);
  overflow-y: scroll;
  .item {
    justify-content: space-between;
    margin-bottom: 0.2rem;
    background-color: #f5f5f5;
    &:nth-child(1) {
      margin-top: 0.2rem;
    }
    .head {
      padding: 0.3rem 0.5rem;
      flex-direction: row;
      border-bottom: 1px solid #ccc;
      .name {
        flex: 1;
      }
    }
    .platform {
      flex-direction: row;
      align-items: center;
      padding: 0.3rem 0.5rem;
      .more {
        width: 0.3rem;
        height: 0.3rem;
        cursor: pointer;
        flex-shrink: 0;
        margin-right: 0.2rem;
        transition: transform 0.1s;
        &.avtive{
          transform: rotate(90deg);
        }
        svg {
          fill: #666;
        }
      }
      .logo{
        width: 1.2rem;
        height: 1.2rem;
        margin-right: 0.3rem;
      }
      .name {
        width: 2rem;
        flex-shrink: 0;
      }
      .version {
        flex: 1;
        flex-shrink: 0;
      }
      .tip {
        margin-right: 0.5rem;
        margin-left: 0.5rem;
      }
      .build {
        width: 0.7rem;
        height: 0.5rem;
        cursor: pointer;
        flex-shrink: 0;
        svg {
          fill: #666;
        }
      }
      .loading {
        width: 0.7rem;
        justify-content: center;
      }
    }
    .logs {
      padding: 0.2rem;
      background-color: #333;
      .log {
        color: #fff;
      }
    }
  }
}
